import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button } from '@material-ui/core';
import React from 'react';

interface AboutDialog {
  open: boolean;
  setOpen: (value: boolean) => void;
}

const AboutDialog = ({open, setOpen}: AboutDialog) => {
  const handleClose = () => {
    setOpen(false);
  }

  return (
    <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Acerca de esta app"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Esta app fue desarrollada con mucho 💝 por Tomás García Gobet y Matías Gallo en el 2019. ¿Sabías que
            la app puede funcionar offline? ¡Sí! Si el navegador no te lo ofreció, la podés agregar a tu pantalla
            de inicio. Súper 🆒.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary">
            Ok
          </Button>
        </DialogActions>
      </Dialog>
  )
}

export default AboutDialog;
